@import 'src/common/styles/sass/variables';

@mixin text($fontFamily, $fontSize, $fontWeigh, $lineHeight) {
  font-family: $fontFamily;
  font-size: $fontSize;
  font-weight: $fontWeigh;
  line-height: $lineHeight;
  color: $textColor;
}

